import {Box, ButtonBase, debounce} from '@mui/material';
import CircularProgress from '@mui/material/CircularProgress';
import React, {FC, useCallback, useEffect, useRef, useState} from 'react';
import {playAudio} from '../../../../../utility/playAudio';
import {Round, Task} from '../../../assignments';
import {useEventListener} from '../../../useEventListener';
import {useWebSpeechApi} from '../../../useWebSpeechApi';
import {styles} from './styles';
import {typography} from "../../../../styles/typography";

interface BlindfoldedRoundProps {
    tasks: Task[];
    round: Round;
    totalRounds: number;
    onAnswer: (correct: boolean, givenAnswer: number, time: Date) => void;
    onNext: () => void;
}

export const BlindfoldRound: FC<BlindfoldedRoundProps> = ({tasks, totalRounds, round, onAnswer, onNext}) => {
    const [answering, setAnsweringImpl] = useState<boolean>(false);
    const answeringRef = useRef<boolean>(false);
    const [answered, setAnswered] = useState<boolean>(false);

    useEffect(() => {
        setAnswered(false);

        void playAudio('/sounds/notification.mp3');
    }, [round]);

    const setAnswering = (value: boolean) => {
        answeringRef.current = value;
        setAnsweringImpl(value);
    };

    const submitAnswer = useCallback(async (answer: number) => {
        if (answer >= tasks.length) {
            return;
        }
        setAnswering(true);
        const answerTime = new Date();
        console.log("Answer time:", answerTime);

        const correct = answer === round.answerIndex;

        try {
            // await playAudio('/sounds/you_picked.mp3');
            await playAudio(tasks[answer].audioFile);

            await playAudio('/sounds/it_is.mp3');
            await playAudio(round.task.audioFile);
        } catch (e) {
            console.error("Error playing audio:", e);
        }

        onAnswer(correct, answer, answerTime);

        setAnswered(true);
        setAnswering(false);
    }, [round]);

    const keyboardAnswer = useCallback(async (answer: number) => {
        if (answeringRef.current || answered) {
            return;
        }
        await submitAnswer(answer);
    }, [submitAnswer, setAnswering, answering, answered]);

    const voiceAnswer = useCallback(debounce(async (voiceAnswer: string) => {
        if (answeringRef.current || answered) {
            return;
        }

        const answer = tasks.findIndex(c => c.voice === voiceAnswer);
        console.log('Voice answer:', voiceAnswer, answer);
        if (answer < 0) {
            return;
        }

        await submitAnswer(answer);
    }, 300), [submitAnswer, answered]);

    useWebSpeechApi(!answering, voiceAnswer);

    const onKeyDown = useCallback(async (key: string) => {
        if (answered) {
            // console.log("Key:", key);
            if (key === 'Enter' || key === ' ') {
                // console.log("Next");
                onNext();
            }
            return;
        }

        const parsed = Number.parseInt(key);
        if (Number.isNaN(parsed)) {
            return;
        }

        await keyboardAnswer(parsed - 1);
    }, [submitAnswer, answered, onNext, keyboardAnswer]);

    const nextRound = useCallback(() => {
        if (answered) {
            onNext();
        }
    }, [answered]);

    useEventListener('keydown', (event) => onKeyDown((event as KeyboardEvent).key));

    return <>
        <Box className={styles.fullScreen}
             style={{backgroundColor: round.task.color}}
             onClick={nextRound}>
            {!answered && !answering && <div className={styles.touchControlsContainer}>
                {tasks.map((c, i) =>
                    <ButtonBase
                        className={`${styles.touchControl} ${i < tasks.length - 1 ? styles.touchControlBorder : ''}`}
                        onClick={() => submitAnswer(i)}
                        key={c.color}>
                        <span className={typography.contrastingText}>{i + 1}: {c.label}</span>
                    </ButtonBase>)}
            </div>
            }
            {answering && <div className={styles.centered}>
                <div className={`${styles.answeringProgressContainer} ${styles.withBackground}`}>
                    <CircularProgress/>
                </div>
            </div>}
            {answered && <div className={`${styles.bottom} ${styles.withBackground}`}>
                Press ENTER or SPACE to continue ...
            </div>}
            <div className="task-details">
                {round.order + 1}/{totalRounds}
            </div>
        </Box>
    </>;
};
