// makes playing audio return a promise

import {Howl} from 'howler';

export async function playAudio(file: string) {
    return new Promise<void>((resolve, reject) => {
        const audio = new Howl({
            src: [file],
            onend: function () {
                resolve();
            }
        });

        // audio.onended = () => resolve();
        audio.play();
    });
}