import { useEffect, useMemo, useReducer, useState } from 'react';
import { Task } from './assignments';
import {ALL_TASKS} from "./Sessions/Blindfold/sessions";

declare var webkitSpeechRecognition: any;
declare var webkitSpeechGrammarList: any;

export interface UseWebSpeechApiResult {
    word: string;
}

export const useWebSpeechApi = (enabled: boolean, onUpdate: (word: string) => void): UseWebSpeechApiResult => {
    const [word, setWord] = useState('');
    const [error, setError] = useState(undefined);
    const [retry, incrementRetry] = useReducer((val)=>val + 1, 0);

    const grammarList = useMemo(()=> {
        if (typeof webkitSpeechGrammarList === "undefined") {
            return undefined;
        }

        const grammar = `#JSGF V1.0; grammar colors; public <color> = ${ALL_TASKS.map((c: Task) => c.voice).join(' | ')};`;
        const grammarList = new webkitSpeechGrammarList();
        grammarList.addFromString(grammar, 1);
        return grammarList;
    }, []);

    useEffect(() => {
        if (typeof webkitSpeechRecognition === "undefined") {
            return;
        }
        // new instance of speech recognition
        const recognition = new webkitSpeechRecognition();
        // set params

        recognition.grammars = grammarList;

        recognition.continuous = true;
        recognition.interimResults = true;

        setError(undefined);

        if (enabled) {
            recognition.start();
        }

        recognition.onresult = function (event: any) {
            // delve into words detected results & get the latest
            // total results detected
            const resultsLength = event.results.length - 1;
            // get length of latest results
            const arrayLength = event.results[resultsLength].length - 1;
            // get last word detected
            const saidWord = event.results[resultsLength][arrayLength].transcript?.trim();

            setWord(saidWord);
            onUpdate(saidWord);
        };

        // speech error handling
        recognition.onerror = function (event: any) {
            // console.log('error?');
            // console.log("Error:", event);
            if (event.error === "no-speech") {
                incrementRetry();
            } else {
                setError(event.error);
            }
        };

        return () => {
            if (enabled) {
                recognition.stop();
            }
        };
    }, [onUpdate, retry, grammarList, enabled]);

    return { word };
};
