import {css} from '@emotion/css';
import LoadingButton from '@mui/lab/LoadingButton';
import TextField from '@mui/material/TextField';
import React, {FC, useCallback, useEffect, useState} from 'react';
import {Link, useHistory, useLocation} from 'react-router-dom';
import {useAuth} from '../../../useAuth';
import {containers} from '../../styles/containers';
import {typography} from '../../styles/typography';
import {Button, Card, CardContent} from "@mui/material";

const styles = {
    formField: css`
      margin-bottom: 1em;
    `,
};

interface UseLoginResult {
    loading: boolean;
    login: (login: string, password: string) => void;
}

const useLogin = (): UseLoginResult => {
    const history = useHistory();
    const location = useLocation();
    const auth = useAuth();

    const {from} = location.state as any || {from: {pathname: '/'}};
    const [loading, setLoading] = useState<boolean>(false);

    const login = useCallback((login: string, password: string) => {
        setLoading(true);
        auth?.signin(() => {
            setLoading(false);
            history.replace(from);
        });
    }, [auth, history, from]);

    return {login, loading};
};

const handleChange = (setter: (value: string) => void) => (event: React.ChangeEvent<HTMLInputElement>) => {
    setter(event.target.value);
};

export const LoginPage: FC<any> = () => {
    const {login, loading} = useLogin();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    return (
        <>
            <h1 className={typography.appTitle}>Puremagic</h1>
            <div className={containers.narrow}>

                <Card>
                    <CardContent>
                        <h1>Sign in</h1>
                        <p>Enter the world of pure magic!</p>
                        <div className={styles.formField}>
                            <TextField label="E-mail" variant="outlined" fullWidth
                                       value={email} onChange={handleChange(setEmail)}/>
                        </div>
                        <div className={styles.formField}>
                            <TextField label="Password" variant="outlined" type="password" fullWidth
                                       value={password} onChange={handleChange(setPassword)}/>
                        </div>
                        <LoadingButton variant="contained" loading={loading}
                                       onClick={() => login(email, password)} type="button"
                                       fullWidth>Sign in</LoadingButton>
                        <p>Haven't registered with puremagic yet? <Link to="/signup">Sign up here.</Link></p>
                    </CardContent>
                </Card>
            </div>
        </>
    );
};