import React, {FC} from 'react';
import {useEventListener} from '../useEventListener';
import {styles} from './Blindfold/BlindfoldSession/styles';
import {Task} from "../assignments";
import {Box} from "@mui/material";
import {typography} from "../../styles/typography";
import TouchAppRoundedIcon from '@mui/icons-material/TouchAppRounded';

interface StartProps {
    onStart: () => void;
    tasks: Task[];
}

export const PutOnBlindfold: FC<StartProps> = ({onStart, tasks}) => {
    useEventListener('keydown', (event) => onStart());

    return <div className={`${styles.fullScreen} ${styles.withDarkerBackground}`}
                onClick={onStart}>
        <div className={styles.centered}>
            <div>
                <p>Put on your blindfold and press any key or click anywhere to start the exercise.</p>
                <p>Screen:</p>
                {tasks.map(task => <Box key={task.id}
                                        display="flex"
                                        alignItems="stretch"
                                        justifyContent="center"
                                        width="300px"
                                        height="180px"
                                        borderRadius="5px"
                                        mb={1}
                                        marginLeft="auto"
                                        marginRight="auto"
                                        sx={{backgroundColor: task.color}}>
                    {tasks.map(t => <Box key={t.id}
                                         flexDirection="column"
                                         flex={1}
                                         display="flex"
                                         alignItems="center"
                                         justifyContent="center">
                        {task.id === t.id && <>
                            <span className={typography.contrastingText}>{t.label}</span>
                            <TouchAppRoundedIcon fontSize="small"/>
                        </>}
                    </Box>)}
                </Box>)}

                <p>Keyboard:</p>

                <ol>
                    {tasks.map(task => <li key={task.id}>{task.label}</li>)}
                </ol>
            </div>
        </div>
    </div>;
};
