import React, {useCallback, useEffect, useMemo, useState} from 'react';
import {playAudio} from '../../../../../utility/playAudio';
import {Round, Task} from '../../../assignments';
import {PutOnBlindfold} from '../../PutOnBlindfold';
import {SessionRecord, Statistics} from '../../Statistics';
import {BlindfoldRound} from './BlindfoldRound';
import {Link, useLocation} from "react-router-dom";
import {ALL_TASKS} from "../sessions";
import {Clear} from "@mui/icons-material";
import {Box, Button} from "@mui/material";

function useTasks() {
    const {search} = useLocation();

    return useMemo<Task[]>(() => {
        return new URLSearchParams(search).get("tasks")
            ?.split(",")
            .map(t => ALL_TASKS.find(at => at.id == t))
            .filter(t => !!t) as Task[] ?? [];
    }, [search]);
}

export function BlindfoldSession() {
    const [index, setIndex] = useState(0);
    const [started, setStarted] = useState<boolean>(false);
    const [roundStart, setRoundStart] = useState<Date>(new Date());
    const [sessionRecord, setSessionRecord] = useState<SessionRecord>({
        start: new Date(),
        end: new Date(),
        answers: [],
        summary: {},
    });
    const tasks = useTasks();

    const totalRounds = 10;

    useEffect(() => {
        setIndex(0);
    }, [tasks]);

    useEffect(() => {
        console.log("Round start!", new Date());
        setRoundStart(new Date());
        if (index === totalRounds) {
            playAudio('/sounds/game_over.mp3');
        }
    }, [index]);

    const rounds = useMemo(() => {
        const result: Round[] = [];
        for (let i = 0; i < totalRounds; i++) {
            const randomPick = Math.trunc(Math.random() * tasks.length);
            result.push({
                order: i,
                answerIndex: randomPick,
                task: tasks[randomPick],
            });
        }
        return result;
    }, []);

    const onAnswer = (correct: boolean, givenAnswer: number, time: Date) => {
        const correctAnswer = rounds[index].answerIndex;
        sessionRecord.answers.push({
            correct,
            givenAnswer: givenAnswer,
            start: roundStart,
            end: time,
            task: rounds[index].task
        });

        console.log(sessionRecord.answers);

        const {summary} = sessionRecord;
        summary[correctAnswer] = summary?.[correctAnswer] ?? {
            correct: 0,
            wrong: 0,
            task: tasks[correctAnswer]
        };
        if (correct) {
            summary[correctAnswer].correct++;
        } else {
            summary[correctAnswer].wrong++;
        }
        setSessionRecord({...sessionRecord});
    };

    const onNext = useCallback(() => {
        setIndex(index + 1);
    }, [index]);

    if (index >= rounds.length) {
        return <Statistics session={sessionRecord}/>;
    }

    return <>
        <Box position="absolute" sx={{right: 0, top: 0, zIndex: 1}}>
            <Button component={Link} to="/blindfold"><Clear/></Button>
        </Box>

        {started
            ? <BlindfoldRound tasks={tasks}
                              round={rounds[index]}
                              totalRounds={totalRounds}
                              onAnswer={onAnswer}
                              onNext={onNext}/>
            : <PutOnBlindfold tasks={tasks} onStart={() => setStarted(true)}/>}
    </>;
}
