function createData(
    trialCode: string,
    id: string,
    status: string,
) {
    return {trialCode, id, status};
}

const rows = [
    createData('1542-4846/A', '159', 'new'),
    createData('1542-4846/B', '237', 'new'),
    createData('1542-4846/C', '262', 'answered'),
];

function TrialTable() {
    return (
        <>
            <Table aria-label="simple table" size="small">
                <TableHead>
                    <TableRow>
                        <TableCell>Trial</TableCell>
                        <TableCell></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row) => (
                        <TableRow
                            key={row.id}
                            sx={{'&:last-child td, &:last-child th': {border: 0}}}
                        >
                            <TableCell>{row.trialCode}</TableCell>
                            <TableCell align="right">
                                <TrialActionButton id={row.id} status={row.status}/>
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </>
    );
}

interface TrialActionButtonProps {
    id: string;
    status: string;
}

const TrialActionButton: FC<TrialActionButtonProps> = ({id, status}) => {
    if (status === 'new') {
        return <Button variant="contained"
                       href={'/target/' + id}>Open</Button>;
    }

    if (status === 'answered') {
        return <>Answered</>;
    }

    return <></>;
};
