import { useCallback, useState } from 'react';

export const useNewTask = () => {
    const [newTaskId, setNewTaskId] = useState<string | undefined>(undefined);
    const [loading, setLoading] = useState<boolean>(false);

    const getNewTaskId = useCallback(() => {
        setLoading(true);
        setTimeout(() => {
            setLoading(false);
            setNewTaskId((Math.trunc(Math.random() * 1000) + 1) + '');
        }, 1000);
    }, [setLoading, setNewTaskId]);

    return { newTaskId, loading, getNewTaskId };
};