import { ThemeProvider } from '@mui/material';
import React, { FC } from 'react';
import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
import './App.css';
import { BlindfoldExercises } from './Components/Pages/Sessions/Blindfold/BlindfoldExercises';
import { BlindfoldSession } from './Components/Pages/Sessions/Blindfold/BlindfoldSession/BlindfoldSession';
import { BlindfoldSettings } from './Components/Pages/Sessions/Blindfold/BlindfoldSession/BlindfoldSettings';
import { HomePage } from './Components/Pages/HomePage/HomePage';
import { LoginPage } from './Components/Pages/LoginPage/LoginPage';
import { SignUpPage } from './Components/Pages/SignUpPage/SignUpPage';
import { TargetPage } from './Components/Pages/TargetPage/TargetPage';
import { containers } from './Components/styles/containers';
import { theme } from './Components/styles/theme';
import { ProvideAuth, useAuth } from './useAuth';
import {Brain} from "./Components/Pages/Sessions/Brain/Brain";

export function App() {
    return (
        <ThemeProvider theme={theme}>
            <div className={containers.app}>
                <ProvideAuth>
                    <Router>
                        <Switch>
                            <Route path="/login">
                                <LoginPage/>
                            </Route>
                            <Route path="/blindfold/session">
                                <BlindfoldSession/>
                            </Route>
                            <Route path="/blindfold">
                                <BlindfoldExercises/>
                            </Route>
                            <Route path="/brain">
                                <Brain/>
                            </Route>
                            <Route path="/settings">
                                <BlindfoldSettings/>
                            </Route>

                            <Route path="/signup">
                                <SignUpPage/>
                            </Route>

                            <Route path="/target/:targetId">
                                <TargetPage/>
                            </Route>

                            <PrivateRoute path="/" exact>
                                <HomePage/>
                            </PrivateRoute>
                        </Switch>
                    </Router>
                </ProvideAuth>
            </div>
        </ThemeProvider>
    );
}

// A wrapper for <Route> that redirects to the login
// screen if you're not yet authenticated.
const PrivateRoute: FC<any> = ({ children, ...rest }) => {
    let auth = useAuth();
    return (
        <Route
            {...rest}
            render={({ location }) =>
                auth.user ? (
                    children
                ) : (
                    <Redirect
                        to={{
                            pathname: '/login',
                            state: { from: location }
                        }}
                    />
                )
            }
        />
    );
};
