import { css } from '@emotion/css';
import { Box, Button, Grid, Slider } from '@mui/material';
import React, { FC, useState } from 'react';
import { TargetProps } from '../TargetAssignment/TargetAssignment';

const styles = {
    imageContainer: css`
      height: 20em;
      padding: 0.3em;
    `,
    image: css`
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 0.3em;
    `,
    imageBlur: (certainty: number) => css`
      filter: grayscale(${certainty / 100}) brightness(${1 - certainty / 100}) /* blur(${certainty / 100}) */;
        //opacity: ${1 - certainty / 100};
    `
};

const getSliderLabel = (value: number) => value === 0
    ? '?'
    : `Picture ${(value > 0 ? 'A' : 'B')}: ${Math.abs(value)}%`;

export const TargetChoice: FC<TargetProps> = ({ target }) => {
    const [slider, setSlider] = useState(0);

    return <>
        <Grid container style={{ marginBottom: '1em' }}>
            <Grid item flex={1}>
                <Box className={styles.imageContainer}>
                    <img className={styles.image + ' ' + styles.imageBlur(slider > 0 ? 0 : -slider)}
                         src="https://upload.wikimedia.org/wikipedia/commons/c/cb/Toulouse_Matabiau_pilgrim_train_2017_2.jpg"/>
                </Box>
                <Box className={styles.imageContainer}>
                    <img className={styles.image + ' ' + styles.imageBlur(slider < 0 ? 0 : slider)}
                         src="https://upload.wikimedia.org/wikipedia/commons/1/1c/Maastricht%2C_C%C3%A9ramique-terrein_na_sloop%2C_betonrecyclage.jpg"/>
                </Box>
            </Grid>
            <Grid item>
                <Slider orientation="vertical" track={false}
                        defaultValue={0}
                        valueLabelDisplay="on"
                        getAriaValueText={getSliderLabel}
                        valueLabelFormat={getSliderLabel}
                        marks
                        step={10}
                        min={-100}
                        max={100}
                        onChange={(e, value) => setSlider(value as number)}
                />
            </Grid>
        </Grid>
        <Grid container justifyContent="end">
            <Grid item>
                <Button variant="contained"
                        href={'/target/' + target.id + '/submit'}>Submit choice</Button>
            </Grid>
        </Grid>
    </>;
};
