import {
    Box,
    Paper,
    Stack,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableFooter,
    TableHead,
    TableRow
} from '@mui/material';
import React, {FC, useMemo} from 'react';
import {Task} from '../assignments';
import {styles} from './Blindfold/BlindfoldSession/styles';
import CheckIcon from '@mui/icons-material/Check';
import ClearIcon from '@mui/icons-material/Clear';

export interface AnswerRecord {
    start: Date;
    end: Date;
    task: Task;
    givenAnswer: number;
    correct: boolean;
}

export interface SessionRecord {
    start: Date;
    end: Date;
    answers: AnswerRecord[];
    summary: Record<string, Statistic>;
}

export interface Statistic {
    task: Task;
    correct: number;
    wrong: number;
}

interface StatisticsProps {
    session: SessionRecord;
}

export const Statistics: FC<StatisticsProps> = ({session: {summary, answers}}) => {
    const {totalTasks, totalCorrect} = useMemo(() => {
        let totalCorrect = 0;
        let totalWrong = 0;

        for (const s of Object.values(summary)) {
            totalCorrect += s.correct;
            totalWrong += s.wrong;
        }

        return {
            totalTasks: totalCorrect + totalWrong,
            totalCorrect,
        };
    }, [summary]);

    return <Box>
        <TableContainer component={Paper} sx={{mb: 1}}>
            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell component="th">Task</TableCell>
                        <TableCell component="th" align="right">Correct</TableCell>
                        <TableCell component="th" align="right">Wrong</TableCell>
                        {/*<TableCell>Wrong</TableCell>*/}
                        <TableCell component="th" align="right">%</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {Object.values(summary).map(e =>
                        <TableRow key={e.task.id}>
                            <TableCell>
                                {e.task.label}&nbsp;
                                {e.task.color &&
                                    <div className={styles.coloredBlock}
                                         style={{backgroundColor: e.task.color}}/>}
                            </TableCell>
                            <TableCell align="right">
                                {[...Array(e.correct)].map((x, i) =>
                                    <CheckIcon key={i} color="success" fontSize="small"/>
                                )}
                            </TableCell>
                            <TableCell align="right">
                                {[...Array(e.wrong)].map((x, i) =>
                                    <ClearIcon key={i} color="success" fontSize="small"/>
                                )}
                            </TableCell>
                            {/*<TableCell align="right">{e.wrong}</TableCell>*/}
                            <TableCell
                                align="right">{e.correct + e.wrong === 0 ? 0 : (e.correct / (e.correct + e.wrong) * 100).toFixed()}%</TableCell>
                        </TableRow>)}
                </TableBody>
                <TableFooter>
                    <TableRow>
                        <TableCell>Total</TableCell>
                        <TableCell align="right">{totalCorrect}</TableCell>
                        <TableCell align="right">{totalTasks - totalCorrect}</TableCell>
                        {/*<TableCell align="right">{totalWrong}</TableCell>*/}
                        <TableCell
                            align="right">{totalTasks === 0 ? 0 : (totalCorrect / (totalTasks) * 100).toFixed()}%</TableCell>
                    </TableRow>
                </TableFooter>
            </Table>
        </TableContainer>
        <Paper>
            <Stack direction="column" gap={1} p={1}>
                {answers.map((answer, i) => <Box>
                    <Stack gap={1} direction="row" key={i}>
                        {answer.correct
                            ? <CheckIcon color="success"/>
                            : <ClearIcon color="success"/>}
                        <Box>{((answer.end.getTime() - answer.start.getTime()) / 1000).toFixed(1)}s</Box>
                    </Stack>
                </Box>)}
            </Stack>
        </Paper>
    </Box>;
};
