import {Box, Button, Checkbox, FormControlLabel, FormGroup, Grid, Paper, Stack} from '@mui/material';
import React, { FC, useState } from 'react';
import { containers } from '../../../../styles/containers';
import {ALL_TASKS} from "../sessions";

interface BlindfoldedSettingsProps {

}

export const BlindfoldSettings: FC<BlindfoldedSettingsProps> = () => {
    const [checked, setChecked] = useState<Record<string, boolean>>({});

    const isChecked = (id: string) => {
        return checked[id];
    };

    const onChange = (id: string, value: boolean) => {
        console.log('Change:', id, value);
        if (value) {
            setChecked({
                ...checked,
                [id]: true,
            });
        } else {
            const tmpChecked = {
                ...checked,
            };
            delete tmpChecked[id];
            setChecked(tmpChecked);
        }
    };

    const getIndex = (id: string) => {
        return checked[id]
            ? Object.keys(checked).findIndex((v) => v === id) + 1
            : '';
    };

    return <Paper className={containers.paper}>
        <FormGroup>
            {ALL_TASKS.map(c =>
                <Stack key={c.id}>
                    <Box alignSelf="stretch" style={
                        { backgroundColor: c.color, width: "1em", margin: "0.2em 0.5em 0.2em 0", borderRadius: "0.3em" }}>
                    </Box>
                    <Box flex={1}>
                        <FormControlLabel label={c.label}
                                          control={<Checkbox value={isChecked(c.id)}
                                                             onChange={(event) => onChange(c.id, event.target.checked)}/>}
                        />
                    </Box>
                    <Box alignSelf="center">
                        {getIndex(c.id)}
                    </Box>
                </Stack>
            )}
        </FormGroup>

        <Button variant="contained">Update settings</Button>
    </Paper>;
};
