import { useEffect, useState } from 'react';

export interface Target {
    id: string;
    code: string;
    images?: string[];
    resolution?: number;
}

interface UseTargetResult {
    loading: boolean;
    target?: Target;
}

export const useTarget = (targetId: string): UseTargetResult => {
    const [loading, setLoading] = useState<boolean>(false);
    const [target, setTarget] = useState<Target | undefined>(undefined);

    useEffect(() => {
        if (!targetId) {
            return;
        }
        setLoading(true);

        setTimeout(() => {
            setLoading(false);
            setTarget({
                id: targetId,
                code: (Math.trunc(Math.random() * 9000) + 1000) + '-' + (Math.trunc(Math.random() * 9000) + 1000),
            });
        }, 100);
    }, [targetId]);

    return { loading, target };
};
