import { Paper } from '@mui/material';
import React, { FC } from 'react';
import { BrowserRouter, Route, Switch, useParams } from 'react-router-dom';
import { CenteredLoader } from '../../CenteredLoader';
import { useTarget } from './useTarget';
import { TargetAssignment } from './TargetAssignment/TargetAssignment';
import { TargetChoice } from './TargetChoice/TargetChoice';
import { TargetSubmit } from './TargetSubmit/TargetSubmit';

export const TargetPage: FC<any> = () => {
    const { targetId } = useParams<{ targetId: string }>();
    const { target, loading } = useTarget(targetId);

    return <Paper style={{ padding: '1em' }}>
        {loading || !target
            ? <CenteredLoader/>
            :
            <BrowserRouter>
                <Switch>
                    <Route path="/target/:targetId/choice">
                        <TargetChoice target={target}/>
                    </Route>
                    <Route path="/target/:targetId/submit">
                        <TargetSubmit target={target}/>
                    </Route>
                    <Route path="/target/:targetId" exact>
                        <TargetAssignment target={target}/>
                    </Route>
                </Switch>
            </BrowserRouter>}
    </Paper>;
};
